import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as dat from 'lil-gui'

/**
 * Base
 */
// Debug
const gui = new dat.GUI()
gui.hide()

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

/**
 * Galaxy
 */
const parameters = {};
parameters.count = 100000;
parameters.size = 0.01;
parameters.radius = 5;
parameters.branches = 3;
parameters.spin = 1;
parameters.randomness = 1;
parameters.randomnessPower = 2.4;
parameters.insideColor = '#ff6030';
parameters.outsideColor = '#1b3984'
parameters.speed = 0.2;

let geometry = null;
let material = null;
let points = null;

const generateGalaxy = ()=>{
    // 删除掉重新开始
    if(points !== null){
        geometry.dispose();
        material.dispose();
        scene.remove(points);
    }
    geometry = new THREE.BufferGeometry();
    const positions = new Float32Array(parameters.count * 3);
    const colors = new Float32Array(parameters.count * 3);
    const colorInside = new THREE.Color(parameters.insideColor);
    const colorOutside = new THREE.Color(parameters.outsideColor);
    const branchesAngle = Math.PI * 2 / parameters.branches;
    for(let i=0; i< parameters.count;i++){
        const i3 = i * 3;
        const branchNumber = i % parameters.branches;
        const radius = Math.random() * parameters.radius;
        const spinAngle = radius * parameters.spin;

        const randomX =Math.pow(Math.random(),parameters.randomnessPower) * (Math.random()<0.5?1:-1) * parameters.randomness;
        const randomY =  Math.pow(Math.random(),parameters.randomnessPower)* (Math.random()<0.5?1:-1) * parameters.randomness;
        const randomZ = Math.pow(Math.random(),parameters.randomnessPower)* (Math.random()<0.5?1:-1) * parameters.randomness;

        positions[i3    ] = radius * Math.sin(branchesAngle*branchNumber + spinAngle) + randomX;
        positions[i3 + 1] = randomY;
        positions[i3 + 2] = radius * Math.cos(branchesAngle*branchNumber + spinAngle) + randomZ;

        //Color
        const mixedColor = colorInside.clone(); 
        mixedColor.lerp(colorOutside,radius / parameters.radius); // 做渐变色
        colors[i3     ] = mixedColor.r;
        colors[i3 + 1 ] = mixedColor.g;
        colors[i3 + 2 ] = mixedColor.b;
    }
    geometry.setAttribute('position',new THREE.BufferAttribute(positions,3));
    geometry.setAttribute('color',new THREE.BufferAttribute(colors,3));
    material = new THREE.PointsMaterial({
        size: parameters.size,
        sizeAttenuation:true,
        depthWrite:false,
        blending: THREE.AdditiveBlending,
        vertexColors:true
    })

    points = new THREE.Points(geometry,material)
    scene.add(points)
}
generateGalaxy();

gui.add(parameters,'count').max(100000).min(100).step(100).onFinishChange(generateGalaxy)
gui.add(parameters,'size').max(0.1).min(0.001).step(0.001).onFinishChange(generateGalaxy)
gui.add(parameters,'radius').max(20).min(0.01).step(0.01).onFinishChange(generateGalaxy)
gui.add(parameters,'branches').max(20).min(2).step(1).onFinishChange(generateGalaxy)
gui.add(parameters,'spin').max(5).min(-5).step(0.001).onFinishChange(generateGalaxy)
gui.add(parameters,'randomness').max(2).min(0).step(0.001).onFinishChange(generateGalaxy)
gui.add(parameters,'randomnessPower').max(10).min(1).step(0.001).onFinishChange(generateGalaxy)
gui.add(parameters,'speed').max(10).min(0).step(0.001).onFinishChange(generateGalaxy)
gui.addColor(parameters,'insideColor').onFinishChange(generateGalaxy)
gui.addColor(parameters,'outsideColor').onFinishChange(generateGalaxy)

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 3
camera.position.y = 3
camera.position.z = 3
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Animate
 */
const clock = new THREE.Clock()

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()

    points.rotation.y = -elapsedTime * parameters.speed;
    // Update controls
    controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()